export default [{
    path: '/auth/register',
    name: 'Register',
    component: () =>
      import ('@/views/auth/Register')
  },
  {
    path: '/',
    name: 'Home',
    alias: '/topics',
    component: () => import('@/views/Home')
  },
  {
    path: '*',
    redirect: '/'
  },
  {
    path: '/auth/login',
    name: 'Login',
    component: () =>
      import ('@/views/auth/Login')
  },
  // 编辑资料路由
  {
    path: '/users/1/edit',
    component: () =>
      import ('@/views/users/Edit.vue'),
    children: [{
        path: '',
        name: 'EditProfile',
        component: () =>
          import ('@/views/users/Profile.vue'),
        //只有登录了才可以访问这个路由
        meta: {
          auth: true
        }
      },
      // EditAvatar
      {
        path: '/users/1/edit_avatar',
        name: 'EditAvatar',
        component: () =>
          import ('@/views/users/Avatar.vue'),
        meta: {
          auth: true
        }
      },
      // EditPassword
      {
        path: '/users/1/edit_password',
        name: 'EditPassword',
        component: () =>
          import ('@/views/users/Password.vue'),
        meta: {
          auth: true
        }
      }
    ]
  },
  //Create
  {
    path: '/articles/create',
    name: 'Create',
    component: () =>
      import ('@/views/articles/Create'),
    meta: {
      auth: true
    }
  },
  // Edit
  {
    path: '/articles/:articleId/edit',
    name: 'Edit',
    component: () =>
      import ('@/views/articles/Create'),
    meta: {
      auth: true
    }
  },
  {
    path: '/:user',
    component: () =>
      import ('@/views/articles/Column'),
    children: [{
        path: '',
        name: 'Column',
        component: () =>
          import ('@/views/articles/List.vue')
      },
      {
        path: '/articles/:articleId/content',
        name: 'Content',
        component: () =>
          import ('@/views/articles/Content.vue')
      }
    ]
  },

]



